___CSS_LOADER_EXPORT___.push([module.id, `:root{
    --primary-color: rgb(13, 106, 134);
    --primary-color-darker: rgb(9, 48, 56);
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    outline: 0;
}

body{
    background: rgb(13, 106, 134);

}

.container{
    max-width: 640px;
    margin: 50px auto;
    background-color: white;
    padding: 50px;
    font-family: "Open  Sans", sans-serif;
    font-size: 1.3em;
    line-height: 1.5em;
    border-radius: 20px;

}

form input, form label, form button, h1, p{
    display: block;
    width: 100%;
    margin-bottom: 10px
}

form input{
    font-size: 24px;
    height: 50px;
    padding: 0 20px;

}

form input:focus{
    outline: 1px solid var(--primary-color);
}

 .botao-form{
    border: none;
    background-color: var(--primary-color);
    color: white;
    font-size: 18px;
    font-weight: 800;
    height: 50px;
    cursor: pointer;
    margin-top: 30px;
}

form button:hover {
    background-color: var(--primary-color-darker);
}

button{
    border: none;
    background-color: var(--primary-color);
    color: white;
    font-size: 18px;
    font-weight: 800;
    height: 5vh;
    cursor: pointer;
    width: 10vw;
    border-radius: 60px;
    margin: 15px;
}

button:hover{
    background-color: var(--primary-color-darker);
}

section h1{
    margin-bottom: 30px;
}

.options{
    display: flex;
    flex-direction: row;
}

.options input{
    display: inline;
    width: 20%;
    height: 20px;
}

.options label{
    display: inline;
}

.senha-gerada{
    color: var(--primary-color);
    font-size: 1.40em;
    padding: 20px 0px;
}`, "",{"version":3,"sources":["webpack://./src/assets/modelo.css"],"names":[],"mappings":"AAEA;IACI,kCAAkC;IAClC,sCAAsC;AAC1C;;AAEA;IACI,SAAS;IACT,UAAU;IACV,sBAAsB;IACtB,UAAU;AACd;;AAEA;IACI,6BAA6B;;AAEjC;;AAEA;IACI,gBAAgB;IAChB,iBAAiB;IACjB,uBAAuB;IACvB,aAAa;IACb,qCAAqC;IACrC,gBAAgB;IAChB,kBAAkB;IAClB,mBAAmB;;AAEvB;;AAEA;IACI,cAAc;IACd,WAAW;IACX;AACJ;;AAEA;IACI,eAAe;IACf,YAAY;IACZ,eAAe;;AAEnB;;AAEA;IACI,uCAAuC;AAC3C;;CAEC;IACG,YAAY;IACZ,sCAAsC;IACtC,YAAY;IACZ,eAAe;IACf,gBAAgB;IAChB,YAAY;IACZ,eAAe;IACf,gBAAgB;AACpB;;AAEA;IACI,6CAA6C;AACjD;;AAEA;IACI,YAAY;IACZ,sCAAsC;IACtC,YAAY;IACZ,eAAe;IACf,gBAAgB;IAChB,WAAW;IACX,eAAe;IACf,WAAW;IACX,mBAAmB;IACnB,YAAY;AAChB;;AAEA;IACI,6CAA6C;AACjD;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,eAAe;IACf,UAAU;IACV,YAAY;AAChB;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,2BAA2B;IAC3B,iBAAiB;IACjB,iBAAiB;AACrB","sourcesContent":["@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300..800;1,300..800&display=swap');\r\n\r\n:root{\r\n    --primary-color: rgb(13, 106, 134);\r\n    --primary-color-darker: rgb(9, 48, 56);\r\n}\r\n\r\n*{\r\n    margin: 0;\r\n    padding: 0;\r\n    box-sizing: border-box;\r\n    outline: 0;\r\n}\r\n\r\nbody{\r\n    background: rgb(13, 106, 134);\r\n\r\n}\r\n\r\n.container{\r\n    max-width: 640px;\r\n    margin: 50px auto;\r\n    background-color: white;\r\n    padding: 50px;\r\n    font-family: \"Open  Sans\", sans-serif;\r\n    font-size: 1.3em;\r\n    line-height: 1.5em;\r\n    border-radius: 20px;\r\n\r\n}\r\n\r\nform input, form label, form button, h1, p{\r\n    display: block;\r\n    width: 100%;\r\n    margin-bottom: 10px\r\n}\r\n\r\nform input{\r\n    font-size: 24px;\r\n    height: 50px;\r\n    padding: 0 20px;\r\n\r\n}\r\n\r\nform input:focus{\r\n    outline: 1px solid var(--primary-color);\r\n}\r\n\r\n .botao-form{\r\n    border: none;\r\n    background-color: var(--primary-color);\r\n    color: white;\r\n    font-size: 18px;\r\n    font-weight: 800;\r\n    height